body {
	font-family: Roboto, Arial, sans-serif;
	color: #333; }

label {
	font-weight: normal; }

* {
	outline: none; }

.wrapper {
	display: flex;
	width: 1190px;
	margin-left: auto;
	margin-right: auto; }
.center-block {
	width: 780px;
	padding-left: 18px;
	padding-right: 18px;
	border-right: 1px solid #e3e3e3; }
.left-block {
	width: 206px;
	border-right: 1px solid #e3e3e3; }
.right-block {
	width: 206px; }

.main-forms {
	margin-left: -25px;
	&-container {
		padding-left: 25px;
		width: 50%;
		float: left; } }

